import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);
import Img1 from '../components/img-1/Img1';
import Img2 from '../components/img-2/Img2';

export default new Router({
  routes: [
    {
      path: '/img1',
      name: 'img1',
      component: Img1
    },
    {
      path: '/img2',
      name: 'img2',
      component: Img2
    }
  ]
})